

.container {
  max-width: 1240px;
  margin: 1rem auto 4rem;
  padding: 0 20px;
}

.heading {
  font-size: 23px;
  margin-bottom: 20px;
  text-transform: uppercase;
}

.accordion {
  list-style: none;
  max-width: 1023px;
  margin: 1rem auto 8rem;
  padding-left: 0;
}

.accordion_item {
  border-top: 1px solid #fff;
  margin-bottom: 28px;
}

.button {
  font-size: 24px;
  background-color: #FF6060;
  color: #fff;
  text-align: left;
  font-weight: 500;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 8px 18px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
}

.control {
  font-size: 20px;
}

.answer {
  background-color: #fff;
  padding: 20px;
  color: #FF6060;
  font-size: 24px;
  line-height: 142.6%;
  background: #F7F7F7;
  border-radius: 5px;
  top: 20.65%;
}

/* activate toggle */
.accordion_item.active .button {
  background-color: #FF6060;
}
.answer_wrapper {
  height: 0;
  overflow: hidden;
  transition: height ease 0.2s;
}

@media (max-width: 767px) {
  .answer {
    font-size: 12px;
  }
}